.title {
    background-color: #5c85d8;
}

.title p {
    margin:0px;
    text-align: center;
    padding: 15px 0px;
    color: #fff;
    font-size: 18px;
}

.card_img_height {
    display: flex;
    background-color: #fff !important;
    justify-content: center;
    object-fit: cover !important;
    background-size: cover !important;
    width: 100%;
}

.card_img_height img {
    width: 100% !important;
}

@media only screen and (min-device-width : 768px) and (max-device-width : 1024px) {
    .card_img_height {
        display: flex;
        background-color: #fff !important;
        justify-content: center;
        object-fit: cover !important;
        background-size: cover !important;
        width: 100%;
        height: 420px !important;
    }
}

