    scroll-behavior: smooth !important;
}

.sidebar img {
    width: 160px;
    padding: 6px;
    margin-left: 40px;
}

.sidebar {
    background-color: #242e3a !important;
    position: fixed !important;
    top: 0 !important;
    z-index: 1;
}

.sidebar_main_title {
    margin-left: 2.50rem !important;
}

.incube_list .MuiTypography-body1 {
    font-size: 12px!important;
    font-weight: 400;
    line-height: 1.5 !important;
    letter-spacing: .00938em;
    color: #fff !important;
    scroll-behavior: smooth !important;
}

.incube_list {
