    margin-top: 0px !important;
}

.sidebar_footer {
    position: fixed !important;
    line-height: 3rem !important;
    bottom: 0 !important;
    background-color: #17202A !important;
    width: 100% !important;
    color: white !important;
    z-index: 1 !important;
