.run_title {
  display: flex;
  justify-content: center;
  cursor: pointer;
}

.run_title_line {
  font-size: 15px;
  font-weight: 600;
  color: #1f3864;
}

.bubbleChart_icon {
  margin-top: 8px;
  font-size: 2.4rem !important;
  cursor: pointer;
  margin-left: 0.7rem;
  color: #1f3864;
}

.yes_button {
  background-color: green !important;
  height: 40px !important;
  color: #fff !important;
  cursor: pointer;
  width: 40px !important;
  text-align: center !important;
  border-radius: 50% !important;
  font-size: 30px;
  margin: auto;
}
  
.no_button {
  background-color: #c02222 !important;
  height: 40px !important;
  cursor: pointer;
  color: #fff !important;
  width: 40px !important;
  text-align: center !important;
  border-radius: 50% !important;
  font-size: 31px;
  margin: auto
}
  
.title_color {
  color: #1f3864;
}
  
.button_height {
  margin-top: -1rem !important;
}
  
.MuiDialogActions-root {
  padding-bottom: 1.5rem !important;
}

.IsRunBtn {
  color: gray !important;
  font-size: 15px;
  font-weight: 600;
  cursor: not-allowed !important;
}
