.crop_detail {
    display: flex !important;
    justify-content: center;
}

.crop_detail_title_heading {
    background-color: #e6e9ee !important;
}

.crop_detail_title {
    text-align: center;
    cursor: pointer;
    font-size: 15px;
    font-weight: 600;
    color: #1f3864;
}

.description_icon {
    margin-top: 9px;
    font-size: 1.9rem !important;
    cursor: pointer;
    margin-left: 0.7rem;
    color: #1f3864;
}

.crop_detail_model_width .MuiDialog-paperWidthSm {
    width: 1200px !important;
}

.back_text_center {
    margin-left: 1rem !important;
} 

.back_icon {
    background-color: #1f3864!important;
    height: 40px !important;
    color: #fff !important;
    width: 40px !important;
    text-align: center !important;
    cursor: pointer;
    border-radius: 50% !important;
}

.go_back_icon {
    margin-top: 7px !important;
    font-size: 28px !important;
}

.crop_title {
    text-align: center !important;
    margin-top: 5px !important;
    font-size: 19px !important;
    color: #1f3864 !important;
}

.edit_text_center {
    margin-left: 1.5rem !important;
}

.edit_icon {
    background-color: #1f3864!important;;
    height: 40px !important;
    text-align: center !important;
    width: 40px !important;
    cursor: pointer;
    color: #fff !important;
    border-radius: 50% !important;
}

.go_edit_icon {
    margin-top: 9px !important;
}

.input_field {
    align-items: center;
}

.temp_label {
    margin-top: 15px;
}

.co2_label {
    margin-top: 15px;
}

.humidity_label {
    margin-top: 15px;
}

.electrolyte_label {
    margin-top: 15px;
}

.deep_red_label {
    margin-top: 15px;
}

input[type="date"] {
    cursor: pointer !important;
}

.end_date_label {
    margin-top: 15px;
}

.start_date_label {
    margin-top: 15px;
}

.btn_color {
    background-color: #1f3864 !important;
    margin-top: 20px !important;
}

.text_color {
    color: #fff;
}

.text_left.MuiDialogActions-root {
    justify-content: center !important;
    display: flex !important;
}

.IscropdetailBtn {
    color: rgb(185, 162, 162) !important;
    font-size: 15px;
    font-weight: 600;
    cursor: not-allowed !important;
}
















