.delete_title {
    display: flex;
    justify-content: center;
    cursor: pointer;
}

.delete_line {
    font-size: 15px;
    font-weight: 600;
    color: #1f3864;
}

.delete_icon {
    margin-top: 11px;
    font-size: 1.8rem!important;
    cursor: pointer;
    margin-left: .7rem;
    color: #1f3864;
}

.IsdeleteBtn {
    color: gray !important;
    font-size: 15px;
    font-weight: 600;
    cursor: not-allowed !important;
}

.delete_api_error {
    text-align: center !important;
    margin-top: 10px;
    font-size: 14px !important;
}