.card_box_shadow {
    box-shadow: 0 10px 30px 0 rgba(172, 168, 168, 0.43) !important;
    border-radius: 20px !important;
    margin-top: 10px !important;
    margin-bottom: 50px !important;
}
 
.graph_title {
    margin-top: 10px !important;
    margin-bottom: 10px !important;
    text-align: center !important;
    font-size: 17px !important;
    font-weight: 600 !important;
    color: #1f3864 !important;
}

.graph .chartjs-render-monitor {
    width: 900px !important;
    height: 350px !important;
} 
  
.duration_title {
    text-align: center !important;
    font-size: 16px !important;
    transform: rotate(-90deg) !important;
    margin: auto !important;
    font-weight: 600 !important;
    margin-left: -20px !important;
    color: #1f3864 !important;
}
   
.hour_line {
    text-align: center !important;
    margin-bottom: 10px !important;
    font-weight: 600 !important;
    color: #1f3864 !important;
    padding-top: 12px !important;
}

.graph_manual_toggal {
    margin-left: 8.8rem !important;
    margin-bottom: 1.5rem !important;
    margin-top: 1rem !important; 
}

.graph_manual_title .MuiTypography-body1 {
    margin-top: 1rem;
    font-size: 17px;
    margin-left: 7.8rem;
}

.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 30px;
}
  
.switch input { 
    opacity: 0;
    width: 0;
    height: 0;
}

.graph_auto_toggal {
    margin-bottom: 1.5rem !important;
    margin-top: 1rem !important;
    margin-left: 9.3rem; 
}

.graph_auto_title .MuiTypography-body1 {
    margin-top: 18px;
    font-size: 17px;
    margin-left: 8.5rem; 
}

.edit_btn_color {
    color: #fff !important;
    margin-bottom: .5rem !important;
    margin-top: 13px !important;
    margin-left: 9.5rem !important;
}

.graph_edit_icon {
    text-align: center !important;
    font-size: 1.7rem !important;
    margin-top: 6px;
}

.round_edit_icon {
    background-color: #1f3864 !important;
    height: 40px !important;
    width:40px !important;
    text-align: center !important;
    cursor: pointer;
    color: #fff !important;
    border-radius: 50% !important;
}

@media screen and (max-width: 360px) {
    .graph .chartjs-render-monitor {
        width: 900px !important;
        height: 350px !important;
    } 
}

@media screen and (min-width: 360px) and (max-width: 575px) {
    .graph .chartjs-render-monitor {
        width: 900px !important;
        height: 350px !important;
    } 
}

@media screen and (min-width: 576px) and (max-width: 767px) {
    .graph .chartjs-render-monitor {
        width: 900px !important;
        height: 350px !important;
    } 
}

@media only screen and (min-device-width:768px) and (max-device-width : 1024px) {
    .graph .chartjs-render-monitor {
        width: 588px !important;
        height: 350px !important;
    }
}

@media screen and (min-width: 1440px) {
    .graph .chartjs-render-monitor {
        width: 950px !important;
    } 
}

@media screen and (min-width: 1600px) {
    .graph .chartjs-render-monitor {
        width: 1050px !important;
        /* margin-left: 9% !important; */
    } 
    .slider_line .MuiSlider-root {
        margin-top: 10px !important;
        margin-left: 9.2% !important;
        width: 85.8% !important;
        color: rgb(30, 144, 255);
    }
}

